<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isShow">
      <el-form :model="form">
        <el-form-item label="角色名称" :label-width="width">
          <el-input v-model="form.rolename" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="角色权限" :label-width="width">
          <el-tree
            :data="list"
            show-checkbox
            node-key="id"
            ref="tree"
            :props="{ children: 'children', label: 'title' }"
          ></el-tree>
        </el-form-item>
        <el-form-item label="状态" :label-width="width">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add" v-if="info.isAdd">添加</el-button>
        <el-button type="primary" @click="update" v-else>修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { reqRoleAdd, reqRoleUpdate, reqRoleListOne } from "../../util/request";
import { mapGetters, mapActions } from "vuex";
import { alertSuccess, alertWarning } from "../../util/alert";
export default {
  computed: {
    ...mapGetters({
      list: "menu/list"
    })
  },
  props: ["info"],
  components: {},
  data() {
    return {
      width: "120px",
      form: {
        rolename: "",
        menus: "",
        status: 1
      }
    };
  },
  methods: {
    //验证
    checked() {
      return new Promise((resolve, reject) => {
        //验证数据是否均不为空
        if (this.form.rolename === "") {
          alertWarning("角色名称不能为空");
          return;
        }
        if (this.$refs.tree.getCheckedKeys().length === 0) {
          alertWarning("选择不能为空");
          return;
        }
        resolve();
      });
    },
    ...mapActions({
      requestMenuList: "menu/requestMenuList",
      requestRoleList: "role/requsetRoleList"
    }),
    look(id) {
      reqRoleListOne({ id: id }).then(res => {
        this.form = res.data.list;
        this.form.id = id;
        this.$refs.tree.setCheckedKeys(JSON.parse(res.data.list.menus));
      });
    },
    empty() {
      this.form = {
        rolename: "",
        menus: "",
        status: 1
      };
    },
    cancel() {
      this.$emit("cancel");
    },
    update() {
      this.checked().then(() => {
        this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys());
        reqRoleUpdate(this.form).then(res => {
          alertSuccess(res.data.msg);
          this.info.isShow = false;
          this.requestMenuList();
          this.requestRoleList();
        });
      });
    },
    add() {
      this.checked().then(() => {
        this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys());
        reqRoleAdd(this.form).then(res => {
          alertSuccess(res.data.msg);
          this.info.isShow = false;
          this.requestMenuList();
          this.empty();
          this.requestRoleList();
        });
      });
    }
  },
  mounted() {
    this.requestMenuList();
  }
};
</script>
<style scoped>
</style>


