<template>
  <div>
    <el-button type="primary" @click="willAdd">添加</el-button>
    <v-add :info="info" @cancel='cancel' ref="add"></v-add>
    <el-table
      :data="roleList"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column prop="id" label="角色编号" width="180"></el-table-column>
      <el-table-column prop="rolename" label="角色名称" width="230"></el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1">启用</el-button>
          <el-button type="danger" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import vAdd from "./add.vue";
import {mapActions, mapGetters} from 'vuex'
import {reqRoleDel} from '../../util/request'
export default {
    computed: {
      ...mapGetters({
          roleList:'role/role'
      })  
    },
  components: {
    vAdd
  },
  data() {
    return {
      info: {
        isShow: false,
        title: "",
        isAdd: true
      },
    };
  },
  methods: {
    willAdd() {
      this.$refs.add.empty(),
      this.info = {
        isShow: true,
        title: "角色添加",
        isAdd: true
      };
    },
    ...mapActions({
        requsetRoleList:'role/requsetRoleList'
    }),
    cancel(){
      this.info.isShow= false
    },
    del(id){
      this.$confirm("是否删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(()=>{
        reqRoleDel({ id: id }).then(res=>{
        if(res.data.code==200){
          this.$message({
                type: "success",
                message: "删除成功!",
              });
              this.requsetRoleList();
        }
      })
      })
      .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    edit(id){
        this.info = {
        isShow: true,
        title: "角色修改",
        isAdd: false
      };
      this.$refs.add.look(id)
    }
  },
  mounted() {
      this.requsetRoleList();
  }
};
</script>
<style scoped>
</style>


